export default{
	data(){
		return{
			isShow:false,
			currentIndex:0, //当前左侧被选择的id
			goodsList:[], //全部分类，包括一二级类目
			currentData:[], //二级类目
				
		}
	},
	onLoad(){
		this.getClass();
	},
	methods:{
		getList(key){
			//根据二级类目查询商品
			const _this = this;
			uni.hideKeyboard()
			 uni.setStorage({
			     key: 'search_key',
			     data: key,
			     success: function () {
			         _this.$CommonJS.navigateTo('../search/searchDetail');
			     }
			 });
		},
		getClass(){
			//获取类目
			uni.request({
			    url: this.$API.URL + this.$API.spk_cate, 
			    success: (res) => {
						const d = res.data.msg.data;
							//console.log(d)
							this.goodsList = d;
							this.currentData = d[0].child; //c初始化右侧列表
			    }
			});
		},
		goSearch(){
			//搜索
			this.$CommonJS.navigateTo('../search/search')
		},
		selectGoods(index){
			//左侧导航切换
			this.currentIndex = index;
			//显示右侧内容
			this.currentData = this.goodsList[index].child;
		},
		
		goback:function(){
			//返回按钮
			this.$CommonJS.navigateBack();
		}
	}
}
